import {Observable, throwError} from 'rxjs';
import {Injectable} from '@angular/core';
import {
  HttpErrorResponse,
  HttpEvent,
  HttpHandler,
  HttpInterceptor,
  HttpRequest,
  HttpResponse
} from '@angular/common/http';
import {catchError, map} from 'rxjs/operators';
import {NgxSpinnerService} from "ngx-spinner";

@Injectable()
export class LoaderInterceptor implements HttpInterceptor {
  private registeredEndpoints = [];

  constructor(private spinnerService: NgxSpinnerService) {
  }

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

    return next
      .handle(this.processRequest(request))
      .pipe(map((response: HttpResponse<any>) => this.processResponse(request, response)),
        catchError(response => {
          if (response instanceof HttpErrorResponse) {
            this.registeredEndpoints = [];
            this.spinnerService.hide();
          }
          return throwError(response);
        }));
  }


  private processRequest(request: HttpRequest<any>) {
    if (request.url || request.urlWithParams) {
      this.registeredEndpoints.push(request);
      this.spinnerService.show();
    }
    return request;
  }

  private processResponse(request: HttpRequest<any>, response: HttpResponse<any>) {
    if (response.url) {
      this.registeredEndpoints = this.registeredEndpoints.filter(this.urlNotEndsWith(response));
      if (this.registeredEndpoints.length === 0) {
        this.spinnerService.hide();
      }
    }
    return response;
  }

  private urlNotEndsWith(response: HttpResponse<any>) {
    return (request) =>
      !(response.url.endsWith(encodeURI(decodeURI(request.url))) || response.url.endsWith(encodeURI(request.urlWithParams)));
  }
}
