import {Component, OnInit} from '@angular/core';
import {MatTableDataSource} from '@angular/material/table';
import {FormControl, Validators} from '@angular/forms';
import {AlertService} from '@full-fledged/alerts';
import {PersonaService} from '../../services/persona.service';

const SLOPE = 3.3363697569700348e-06;
const INTERCEPT = 1932.2573852507373;

const digitos = 999;

interface Datos {

  rut?;
  nombre?;
  paterno?;
  materno?;
  fecha?;
  email?;
  nacimiento?;
  edad?;

}

@Component({
  selector: 'app-inicio',
  templateUrl: './inicio.component.html',
  styleUrls: ['./inicio.component.scss']
})
export class InicioComponent implements OnInit {


  displayedColumns: string[] = ['rut', 'nombre', 'paterno', 'materno', 'email', 'nacimiento', 'edad'];
  dataSource = new MatTableDataSource<any>([]);
  // }
  opcion = 'ambos';
  //   return this.months[month]
  inputForm: FormControl = new FormControl('', [Validators.required]);

  constructor(private personasService: PersonaService, private alertService: AlertService) {
  }

  ngOnInit(): void {
    this.generarPersonas();
  }


  generarPersonas(): any {
    this.dataSource.data = this.personasService.generar(this.inputForm.value, this.opcion);
  }

  messageAlert(message): any {
    this.personasService.copyValue(message);
  }
}
