import {Component, OnInit} from '@angular/core';
import {FormBuilder, Validators} from "@angular/forms";
import {MatTableDataSource} from "@angular/material/table";
import {PdfService} from "../../services/pdf.service";
import Swal from 'sweetalert2'

@Component({
  selector: 'app-enviar',
  templateUrl: './enviar.component.html',
  styleUrls: ['./enviar.component.scss']
})
export class EnviarComponent implements OnInit {
  displayedColumns: string[] = ['rut', 'nombre', 'paterno', 'materno', 'fecha', 'email'];
  dataSource = new MatTableDataSource<any>([]);
  pacientes = []
  public loginForm = this.fb.group({
    rut: ['16115840-2', Validators.required],
    nombre: ['Jos', Validators.required],
    paterno: ['Gajardo', Validators.required],
    materno: ['Tobar', Validators.required],
    fecha: ['22-01-2021', Validators.required],
    email: ['joligato@gmail.com', Validators.required]
  });

  constructor(private fb: FormBuilder, private pdfService: PdfService) {
  }

  ngOnInit(): void {
    this.dataSource = new MatTableDataSource<any>(this.pacientes);
  }


  guardar() {


    this.pacientes.unshift(this.loginForm.value)
    this.dataSource.data = this.pacientes;
    this.loginForm.reset('')
  }


  enviar() {

    const body = this.pacientes.map(e => {

      return {
        nombre: `${e.nombre} ${e.paterno} ${e.materno}`,
        rut: e.rut,
        fecha: e.fecha,
        email: e.email
      }
    })
    this.pdfService.createDocument(body).subscribe(e => {

      Swal.fire({
        title: 'Correos enviados Correctamente',
        icon: 'success',
        confirmButtonText: 'ACEPTAR'
      }).then(() => {
        this.loginForm.reset()
        this.dataSource.data = []
        this.pacientes = [];
      })
    })
  }
}
