import {BrowserModule} from '@angular/platform-browser';
import {CUSTOM_ELEMENTS_SCHEMA, NgModule} from '@angular/core';

import {AppRoutingModule} from './app-routing.module';
import {AppComponent} from './app.component';
import {GenerarRutComponent} from './components/generar-rut/generar-rut.component';
import {HeaderComponent} from './shared/header/header.component';
import {FooterComponent} from './shared/footer/footer.component';
import {GenerarNombreComponent} from './components/generar-nombre/generar-nombre.component';
import {GenerarEmailComponent} from './components/generar-email/generar-email.component';
import {InicioComponent} from './components/inicio/inicio.component';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {MaterialModule} from './material/material.module';
import {DocumentoModule} from './documento/documento.module';
import {HttpClientModule} from '@angular/common/http';
import {NgxSpinnerModule} from 'ngx-spinner';
import {httpInterceptorProviders} from './interceptors';
import {RutPipe} from './pipes/rut.pipe';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {AlertModule} from '@full-fledged/alerts';


@NgModule({
  declarations: [
    AppComponent,
    GenerarRutComponent,
    HeaderComponent,
    FooterComponent,
    GenerarNombreComponent,
    GenerarEmailComponent,
    InicioComponent,
    RutPipe,
  ],
  imports: [
    DocumentoModule,
    BrowserModule,
    AppRoutingModule,
    BrowserAnimationsModule,
    MaterialModule,
    HttpClientModule,
    NgxSpinnerModule,
    FormsModule,
    ReactiveFormsModule,
    AlertModule.forRoot({maxMessages: 7, timeout: 10000, positionX: 'left', positionY: 'top'})

  ],
  providers: [httpInterceptorProviders],
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
  bootstrap: [AppComponent]
})
export class AppModule {
}
