import {Component, OnInit} from '@angular/core';
import {FormControl, Validators} from '@angular/forms';
import {Run} from '../../interfaces/rut';
import {PersonaService} from '../../services/persona.service';


@Component({
  selector: 'app-generar-rut',
  templateUrl: './generar-rut.component.html',
  styleUrls: ['./generar-rut.component.scss']
})
export class GenerarRutComponent implements OnInit {


  listaRun: Array<Run> = [];

  inputForm: FormControl = new FormControl('', [Validators.required]);

  constructor(private rutService: PersonaService) {
  }

  ngOnInit(): void {
    this.generarRut();
  }


  generarRut(): void {
    this.listaRun = this.rutService.retornaRut(this.inputForm.value);

  }


  alert(): void {
    this.rutService.copyValue('Run copiado al porta papeles');
  }
}
